/* ==========================================================================
   YITH WishList styles - begin
   ========================================================================== */

#yith-wcwl-popup-message {
    position: absolute; //absolute centering over added item
    margin:0 !important; //absolute centering over added item
    -webkit-transform-origin:0 0;
    transform-origin:0 0;
    @include mkdfTransform(translate(-50%,-50%));
    border: 1px solid #ebebeb;
    border-radius: 0;
    padding: 0 21px;
    line-height: 35px;

    #yith-wcwl-message {
        font-size: 15px;
        line-height: 1.7em;
        color: $default-background-color;
        font-family: $default-text-font;
        letter-spacing: 0.18em;
        font-weight: 600;
        text-transform: uppercase;

        &:before {
            position: relative;
            font-family: 'dripicons-v2';
            content: "\e004";
            top: 2px;
            right: 7px;
        }
    }

 
    //vanishing effect
    &.mkdf-wishlist-vanish-out {
      @include mkdfAnimation(mkdfWishlistvanishOut 1s both);
    }

    @keyframes mkdfWishlistvanishOut {
      0% {
        opacity: 1;
        @include mkdfTransform(scale(1,1) translate(-50%,-50%));
        -webkit-filter: blur(0px);
        filter: blur(0px);
      }

      100% {
        opacity: 0;
        @include mkdfTransform(scale(2,2) translate(-50%,-50%));
        -webkit-filter: blur(10px);
        filter: blur(10px);
      }
    }
}

.mkdf-yith-wcqv-holder {
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
}

.yith-wcwl-add-to-wishlist {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    margin: 0;
    text-align: center;
    padding: 0 24px;
}

.yith-wcwl-add-button{
    @include mkdfTransform(translateZ(0));
}

.yith-wcwl-add-button,
.yith-wcwl-wishlistaddedbrowse,
.yith-wcwl-wishlistexistsbrowse {

    a {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        font-size: 0;
        color: #b3b3b3;
        line-height: 1;

        &:after {
            content: "\f387";
            position: relative;
            display: inline-block;
            font-size: 24px;
            font-family: "IonIcons";
            line-height: inherit;
            -webkit-font-smoothing: antialiased;
            -moz-osx-font-smoothing: grayscale;
        }

        &:hover {
            color: #fff;
        }
    }
}

.yith-wcwl-wishlistaddedbrowse,
.yith-wcwl-wishlistexistsbrowse {
    a {
        &:after {
            content: "\f387";
            font-family: "IonIcons";
            color: #fff;
            font-size: 25px;
            padding: 0 2px;
        }
    }

    span {
        display: none;
    }
}

.yith-wcwl-add-button {
    .ajax-loading {
        display: none !important;
    }
}

.woocommerce-wishlist {

    .wishlist-title{
        display: none;
    }

    .woocommerce-message,
    .woocommerce-info,
    .woocommerce-error {
        position: relative;
        display: block;
        padding: 15px 30px;
        margin: 50px 0 30px;
        line-height: 48px;
        list-style: none;
        background-color: transparent;
        border: 1px solid $default-border-color;

        > a {
            color: $default-text-color;

            &:hover {
                color: $default-heading-color;
            }
        }

        a {
            float: right;
        }
    }

    table.wishlist_table {
        font-size: inherit;
        margin: 18px 0 30px;

        thead {
            display: none;
        }

        tbody {

            tr {
                border: 0;
                border-bottom: 1px solid $default-border-color;
                td {
                    padding: 22px 0;
                    vertical-align: middle;
                    text-align: center;

                    &.wishlist-empty {
                        padding: 20px 0 30px;
                    }

                    &.product-remove {

                        a {
                            color: #000;
                            font-size: 0;

                            &:after {
                                content: "\f404";
                                display: inline-block;
                                position: relative;
                                top: -4px;
                                vertical-align: middle;
                                speak: none;
                                color: inherit;
                                font-family: "Ionicons";
                                font-size: 25px;
                                font-style: normal;
                                font-weight: 400;
                                font-variant: normal;
                                text-transform: none;
                                text-rendering: auto;
                                line-height: 1;
                                -webkit-font-smoothing: antialiased;
                                -moz-osx-font-smoothing: grayscale;
                            }

                            &:hover {
                                color: $default-heading-color;
                            }
                        }
                    }

                    &.product-thumbnail {
                        padding-left: 8px;
                        padding-right: 8px;
                        width: 92px;

                        a, img {
                            display: block;
                        }
                    }

                    &.product-name {
                        text-align: left;
                        padding-left: 20px;

                        a {
                            font-family: $default-text-font;
                            font-size: 15px;
                            font-weight: 300;
                            line-height: 30px;
                            text-transform: capitalize;
                        }

                        .yith-wcqv-button {
                            height: 38px;
                            line-height: 38px;
                            padding: 0 28px;
                            margin: 0;
                        }
                    }

                    &.product-price{
                        font-size: 14px;

                        ins{
                            text-decoration: none;
                        }
                    }

                    &.product-stock-status span{
                        color: $default-text-color !important;
                    }

                    &.product-add-to-cart {

                        a {
                            display: inline-block !important;
                            margin: 0;
                            text-transform: lowercase;
                            font-family: $default-text-font;
                            cursor: pointer;
                            -webkit-border-radius: 0;
                            -moz-border-radius: 0;
                            border-radius: 0;
                            @include mkdfButtonDefaultStyle();
                            font-size: 12px;
                            color: $default-text-color;
                            background-color: transparent;
                            border: 1px solid $default-text-color;
                            white-space: nowrap;
                            z-index: 3;
                            padding: 1px 16px;

                            &:hover{
                                @include mkdfButtonSolidColor();
                            }
                        }
                    }
                }

                @include phone-landscape{
                    td{

                        &.product-name{
                            width: 40%;
                        }
                        &.product-thumbnail,
                        &.product-stock-status{
                            display: none;
                        }
                    }
                }
            }
        }

        .yith-wcwl-share{
            margin: 0;
            display: none   ;
        }
    }
}

.mkdf-single-product-summary {
    .yith-wcwl-add-to-wishlist {
        width: auto;
        background-color: transparent;
        float: left;
        padding: 0;

        .yith-wcwl-add-button,
        .yith-wcwl-wishlistaddedbrowse,
        .yith-wcwl-wishlistexistsbrowse {
            a {
                font-size: 13px;
                color: $default-text-color;
                line-height: 22px;
                margin-top: 30px;

                &:hover{
                    &:after{
                        content: "\f141";
                        font-family: IonIcons;
                    }
                }

                &:after {
                    content: "\f387";
                    font-size: 15px;
                    line-height: 36px;
                    color: $default-heading-color;
                    top: 1px;
                    right: -20px;
                    font-family: Ionicons;
                    width: 36px;
                    height: 36px;
                    background-color: #fef8f7;
                    -webkit-border-radius: 100%;
                    -moz-border-radius: 100%;
                    border-radius: 100%;
                }
            }

            &.show{
                background-color: transparent;
            }
        }
    }
}

.mkdf-wishlist-widget-holder{
    display: inline-block;
    vertical-align: middle;

    a {
        color: $first-main-color;
        font-size: 13px;
        margin: 0 10px;

        .mkdf-wishlist-items-number {
            color: $default-text-color;
            font-size: 12px;
        }

        &:hover {
            color: $default-text-color;
        }
    }
}

.mkdf-light-header {
    .mkdf-wishlist-widget-holder{
        a {
            color: $header-light-color;
            .mkdf-wishlist-items-number {
                color: $header-light-color;
            }

            &:hover{
                opacity: 0.8;
            }
        }
    }
}

.mkdf-dark-header {
    .mkdf-wishlist-widget-holder{
        a {
            color: $header-dark-color;
            .mkdf-wishlist-items-number {
                color: $header-dark-color;
            }

            &:hover{
                opacity: 0.8;
            }
        }
    }
}

/* ==========================================================================
   YITH WishList styles - end
   ========================================================================== */