/* ==========================================================================
   WooCommerce Quantity Inputs styles - begin
   ========================================================================== */

.woocommerce-page .mkdf-content,
div.woocommerce {

    .mkdf-quantity-buttons {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        
        .screen-reader-text {
            position: relative !important;
            width: auto;
            height: auto;
            clip: unset;
            font-size: 13px;
            line-height: 1em;

            &:after {
                content: ':';
                position: relative;
            }
        }

        .mkdf-quantity-input,
        .mkdf-quantity-minus,
        .mkdf-quantity-plus {
            display: inline-block;
            vertical-align: middle;
            color: $default-text-color;
            background-color: transparent;
            text-align: center;
            box-sizing: border-box;
            border: none !important;
        }

        .mkdf-quantity-input {
            position: relative;
            height: 28px;
            width: 14px;
            padding: 0;
            margin: 0 22px 0 0;
            font-family: inherit;
            line-height: 28px;
            outline: 0;
        }

        .mkdf-quantity-minus,
        .mkdf-quantity-plus {
            position: absolute;
            right: 0;
            width: 16px;
            height: 16px;
            line-height: 14px;
            border-left: 0;
            cursor: pointer;
            @include mkdfTransition(color .15s ease-in-out);

            &:hover {
                color: $first-main-color;
            }
        }

        .mkdf-quantity-plus {
            top: 0;
        }

        .mkdf-quantity-minus {
            bottom: 0;
        }
    }
}

.woocommerce-page .mkdf-content,
div.woocommerce {

    input[type="text"],
    input[type="email"],
    input[type="tel"],
    input[type="password"] {
        @extend %input-style;
        font-size: 13px;
        border: none;
        border-bottom: 1px solid #ddd !important;
        padding: 3px 0 !important;
    }

    textarea {
        @extend %input-style;
        font-size: 13px;
        line-height: 20px;
        padding: 3px 0;
        border: none;
        border-bottom: 1px solid #ddd !important;
    }

    .coupon {

        input[type="text"] {
            width: 200px;
        }
    }
}
/* ==========================================================================
   WooCommerce Quantity Inputs styles - end
   ========================================================================== */