/* ==========================================================================
   WooCommerce Dropdown Cart styles - begin
   ========================================================================== */

.mkdf-shopping-cart-holder {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    height: 100%;
    margin: 0 10px;

    .touch & {
        cursor: pointer;
    }

    .mkdf-shopping-cart-inner {
        position: relative;
        display: table;
        height: 100%;
    }

    .mkdf-header-cart {
        position: relative;
        display: table-cell;
        vertical-align: middle;
        line-height: 1;

        .mkdf-cart-icon {
            position: relative;
            display: inline-block;
            vertical-align: top;
        }

        .mkdf-cart-number {
            font-size: 10px;
            width: 16px;
            height: 16px;
            line-height: 18px;
            text-align: center;
            background-color: $default-text-color;
            border-radius: 100%;
            color: $default-background-color;
            display: inline-block;
            position: relative;
            left: -10px;
            top: -5px;
        }

        &.mkdf-header-cart-icon-pack {
            
            .mkdf-cart-icon {
                font-size: 22px;
                line-height: 1em;
    
                * {
                    display: block;
                    line-height: inherit;
        
                    &:before {
                        display: block;
                        line-height: inherit;
                    }
                }
            }
        }

        &.mkdf-header-cart-svg-path {
            
            .mkdf-cart-icon {

                > * {
                    vertical-align: middle;
                }
                
                > *, 
                path {
                    fill: currentColor;
                    stroke: currentColor;
                }
            }
        }
    }

    &:hover {

        .mkdf-shopping-cart-dropdown {
            top: 100%;
            opacity: 1;
            visibility: visible;
        }
    }
}

.mkdf-dark-header .mkdf-page-header > div:not(.mkdf-sticky-header):not(.fixed) .mkdf-shopping-cart-holder .mkdf-header-cart {
    color: $header-dark-color;

    &:hover {
        color: $header-dark-hover-color;
    }
}

.mkdf-light-header .mkdf-page-header > div:not(.mkdf-sticky-header):not(.fixed) .mkdf-shopping-cart-holder .mkdf-header-cart {
    color: $header-light-color;

    &:hover {
        color: $header-light-hover-color;
    }
}

.mkdf-shopping-cart-dropdown {
    position: absolute;
    top: calc(100% + 50px);
    left: auto;
    right: 0;
    width: 318px;
    background-color: #fff;
    text-align: left;
    z-index: 1000;
    opacity: 0;
    visibility: hidden;
    box-sizing: border-box;
    box-shadow: 0 5px 5px 0 rgba(0,0,0,.03);
    @include mkdfTransition(all 0.35s cubic-bezier(.53,.01,.18,1));

    ul {
        margin: 32px 32px 25px;
        padding: 0;

        li {
            @include mkdfTableLayout();
            padding: 20px 0;
            box-sizing: border-box;

            &.mkdf-empty-cart {
                color: initial;
                border-bottom: 0;
            }

            &:first-child {
                padding-top: 0;
            }

            &:last-child {
                padding-bottom: 0;
            }
        }
    }

    .mkdf-item-image-holder {
        position: relative;
        display: table-cell;
        height: 100%;
        width: 105px;
        vertical-align: top;

        img {
            display: block;
        }
    }

    .mkdf-item-info-holder {
        @include mkdfTableCellLayout();
        vertical-align: top;
        padding-left: 18px;

        .mkdf-product-title {
            display: block;
            margin: -5px 0 0;
            padding: 0 20px 0 0;
            box-sizing: border-box;
            @include mkdfP();
        }

        .remove {
            position: absolute;
            top: -3px;
            right: 0;
	        font-size: 22px;
	        line-height: 1;

            span {
                display: block;
                line-height: inherit;

                &:before {
                    display: block;
                    line-height: inherit;
                }
            }
        }

        .mkdf-quantity {
            display: block;
            margin: 0;
            font-size: 13px;
        }

        .amount {
            display: block;
            margin: 3px 0 0;
            color: $default-heading-color;
            font-family: $default-heading-font;
            @include mkdfH6();
        }
    }

    .mkdf-cart-bottom {
        padding: 15px 0 0;

        .mkdf-subtotal-holder {
            position: relative;
            display: table;
            width: 100%;
            margin: 0 0 40px;

            > * {
                display: table-cell;
                vertical-align: top;
                color: $default-heading-color;
                line-height: 20px;

                &.mkdf-total {
                    width: 105px;
                }
            }

            .mkdf-total-amount {
	            font-size: 18px;
                font-family: $default-heading-font;
                font-style: italic;
                letter-spacing: .3em;
                padding-left: 18px;
            }
        }

        .mkdf-btn-holder {
            text-align: center;
        }

        .mkdf-view-cart,
        .mkdf-checkout {
            position: relative;
            display: inline-block;
            @include mkdfButtonDefaultStyle();
            font-size: 12px;
            color: $default-text-color;
            background-color: transparent;
            border: 1px solid $default-text-color;
            white-space: nowrap;
            cursor: pointer;
            z-index: 3;
            padding: 1px 16px;
            text-align: center;
            box-sizing: border-box;
            @include mkdfTransition(all .4s cubic-bezier(0.42, 0, 0.58, 1));

            span {
                display: block;
            }

            &:hover {
                @include mkdfButtonSolidColor();
            }
        }

        .mkdf-view-cart {
            float: left;
        }

        .mkdf-checkout {
            float: right;
        }
    }
}

.mkdf-vertical-menu-area {

    .mkdf-shopping-cart-dropdown {
        display: none !important;
    }
}
/* ==========================================================================
   WooCommerce Dropdown Cart styles - end
   ========================================================================== */