/* ==========================================================================
   WooCommerce Buttons styles - begin
   ========================================================================== */

.woocommerce-page .mkdf-content,
div.woocommerce {
    
    a.button,
    a.added_to_cart,
    input[type="submit"],
    button[type="submit"]:not(.mkdf-search-submit),
    .wc-forward:not(.added_to_cart):not(.checkout-button) {
        @include mkdfButtonDefaultStyle();
        font-size: 12px;
        color: $default-text-color;
        background-color: transparent;
        border: 1px solid $default-text-color;
        cursor: pointer;
        z-index: 3;
        padding: 1px 16px;
        text-indent: 0.5em;

        &:hover {
            @include mkdfButtonSolidColor();
        }
    }
    
    a.button.added {
        display: none;
    }
}
/* ==========================================================================
   WooCommerce Buttons styles - end
   ========================================================================== */