/* ==========================================================================
   WooCommerce Widgets styles - begin
   ========================================================================== */

.widget.woocommerce {
    display: block;
    $thumb: 80px;
    $inner-padding: 15px; // distance between image and content
    $widget-list-item-padding: 15px; // same as sidebar vars

    clear: both;

    li {
        position: relative;
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // title

    .product-title {
        @include mkdfH5();
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // star rating

    li:not(.wc-layered-nav-rating) {
        .star-rating {
            margin-bottom: 7px;
            margin-top: 7px;

            span {
                max-width: 100px;
            }
        }
    }

    li.wc-layered-nav-rating {
        a {
            text-decoration: none;
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // price

    .amount {
        color: inherit;

        span { // currency symbol
            color: inherit;
        }
    }

    del,
    ins {
        display: block;

        // currency symbol
        span {
            color: inherit;
        }
    }

    ins {
        text-decoration: none;
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // layered nav

    &.widget_layered_nav {
        .chosen {
            a {
                color: $first-main-color;
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // price filter

    &.widget_price_filter {
        form {
            margin-top: 64px;
        }

        .price_slider_wrapper {
            .ui-widget-content {
                background-color: #fef8f7;
                height: 4px;
                position: relative;

                .ui-slider-handle {
                    background-color: $default-text-color;
                    border-radius: 50%;
                    cursor: pointer;
                    height: 4px;
                    margin: 0 0 0 -1px;
                    outline: medium none;
                    position: absolute;
                    top: -1px;
                    width: 4px;
                    z-index: 2;

                    &:last-child {
                        @include mkdfTransform(translateX(-50%));
                    }
                }

                .ui-slider-range {
                    background-color: $default-text-color;
                    display: block;
                    height: 2px;
                    position: absolute;
                    top: 0;
                    z-index: 1;
                }
            }
        }

        .price_slider_amount {
            margin: 35px 0 0;

            .button {
                float: right;
                padding-left: 16px;
                padding-right: 16px;
                border: none;
                position: relative;

                &:before {
                    content: '';
                    width: 100%;
                    height: 1px;
                    background-color: #959595;
                    position: absolute;
                    top: 0;
                    left: 0;
                }

                &:hover {
                    background-color: transparent;
                    color: inherit;
                    border: none;
                }
            }

            .price_label {
                float: left;
                font-size: 13px;
                line-height: 16px; // to match button height
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // widgets with images

    &.widget_shopping_cart,
    &.widget_products,
    &.widget_recently_viewed_products,
    &.widget_recent_reviews,
    &.widget_top_rated_products {
        ul {
            li {
                min-height: $thumb;
                position: relative;

                a {
                    img {
                        width: 100%;
                    }

                    span {
                        box-sizing: border-box;
                        display: block;
                        padding: 0 0 0 #{$thumb + $inner-padding};
                        position: relative;
                    }
                }

                > *:not(a) {
                    clear: both;
                    float: left;
                    margin: 0 0 0 #{$thumb + $inner-padding};
                }
            }
        }
    }

    &.widget_product_categories {
        li a {
            position: relative;

            &:after {
                content: '';
                height: 1px;
                width: 100%;
                position: absolute;
                left: 0;
                bottom: 2px;
                background-color: $default-heading-color;
                opacity: 0;
                @include mkdfTransition(all .2s ease);
            }

            &:hover {
                opacity: 1;
            }
        }
    }

    &.widget_products {
        ul li{
            text-align: center;
            margin-bottom: 22px;

            a {

                img {
                    display: block;
                    max-width: 75%;
                    margin: 0 auto 15px;
                }

                span {
                    display: inline-block;
                    font-size: 15px;
                    font-weight: 300;
                    font-family: $default-text-font;
                    text-align: center;
                    font-style: normal;
                    line-height: 1.2em;
                    letter-spacing: .05em;
                    padding: 0;

                    &:after {
                        content: "/";
                        margin: 0 5px 0 7px;
                    }
                }
            }

            .star-rating {
                display: none;
            }

            span.woocommerce-Price-amount {
                display: inline-block;
                float: none;
                margin: 0;
            }

            &:last-child {
                margin-bottom: 0;
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // widgets with images and plain text next to image

    &.widget_shopping_cart,
    &.widget_recent_reviews {
        a {
            display: block;
            padding: 0 0 0 #{$thumb + $inner-padding};

            @include mkdfH5();
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // recent reviews

    //&.widget_recent_reviews {
    //    .reviewer {
    //    }
    //}

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // shopping cart

    &.widget_shopping_cart {
        .remove {
            font-size: 0;
            font-weight: normal;
            line-height: 1;
            position: absolute;
            right: 0;
            top: 5px;
            text-decoration: none;

            &:after {
                content: "\4d";
                display: block;
                font-family: 'ElegantIcons';
                font-size: 15px;
                line-height: 15px;
                position: relative;
            }
        }

        .variation {
            @include mkdfRelativeHolderLayout();

            > * {
                display: inline-block;
                margin: 0;
                vertical-align: middle;

                p {
                    margin: 0;
                }
            }
        }

        .total {
            margin: 17px 0 13px;
        }

        .buttons {
            .button {
                @include mkdfButtonDefaultStyle();
                @include mkdfButtonSize();
                @include mkdfButtonSolidColor();

                &:hover {
                    @include mkdfButtonSolidHoverColor();
                }

                &:first-child {
                    float: left;
                }

                &:last-child {
                    float: right;
                }
            }
        }
    }
}

/* ==========================================================================
   WooCommerce Widgets styles - end
   ========================================================================== */